import test from 'ava';

var java = require("java");
java.classpath.push("class");
java.classpath.push("gson-2.8.0.jar");
var Main = java.import('com.bazaarvoice.Main');
var main = new Main();

test('foo test', t => {

    var res1 = java.callMethodSync(main, "searchResults", "text", "sample");
    console.log("***"+res1+"***");
    t.deepEqual(res1,3,"ERROR!!!");

});


test('Soo test', t => {

    var res2 = java.callMethodSync(main, "searchResults", "text", "hello");
    console.log("***"+res2+"***");
    t.deepEqual(res2,0,"ERROR!!!");

});

test('Roo test', t => {

    var res3 = java.callMethodSync(main, "searchResults", "word", "green");
    console.log("***"+res3+"***");
    t.deepEqual(res3,2,"ERROR!!!");

});